.Layer {
    width: 80px;
    height: 100%;
    float: left;
}
.Layer ~ .Layer {
    width: 200px;
}
.Layer > .neurons, .Layer > .synapses {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    float: left;
}
.Layer > .neurons > * {
    -ms-flex-positive: 0;
        flex-grow: 0;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}
.Layer > .neurons > .padding {
    -ms-flex-positive: 1;
        flex-grow: 1;
}
.Layer > .neurons {
    z-index: 1;
    position: relative;
    width: 80px;
}
.Layer > .synapses {
    position: relative;
    z-index: 0;
    width: 120px;
}



