.page {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #1F1F1F;
    color: #FFFFFF;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.page > .control-bar {
    width: 100vw;
    height: 40px;
    -webkit-box-shadow: 0 -4px 10px 0 #FFFFFF;
            box-shadow: 0 -4px 10px 0 #FFFFFF;
    -ms-flex-positive: 0;
        flex-grow: 0;
}
.page > .control-bar > .shape {
    width: 100%;
    height: 100%;
}
.page > .network {
    width: 100vw;
    -ms-flex-positive: 1;
        flex-grow: 1;
    overflow: auto;
}



